import { useAccordion } from "#Accordion/Context";
import { AccordionTypes } from "#Accordion/Types";
import { triggerCss } from "./styles";

export const Trigger: AccordionTypes['Parts']['Trigger'] = (props) => {
  const { children } = props
  
  const { togglePanel } = useAccordion()

  return (
    <div css={triggerCss(props)} onClick={togglePanel}>
      {children}
    </div>
  )
}